<template>
  <div class="pos">
    <div>
      <el-row>
        <el-col :span='7' class="pos-order" id="order-list">
          <el-tabs>
            <el-tab-pane label='点餐'>
            <!-- show-summary 方法有问题，所以要自行计算 -->
              <el-table :data="tableData" border style="width: 100%" >
                <el-table-column prop="goodsName" label="商品"  ></el-table-column>
                <el-table-column prop="count" label="量" width="50"></el-table-column>
                <el-table-column prop="price" label="金额" width="70"></el-table-column>
                <el-table-column  label="操作" width="100" fixed="right">
                  <template scope="scope">
                    <el-button type="text" size="small" @click="delSingleGoods(scope.row)">删除</el-button>
                    <el-button type="text" size="small" @click="addOrderList(scope.row)">增加</el-button>
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
            <el-tab-pane label='挂单'>
            </el-tab-pane>
            <el-tab-pane label='外卖'>
            </el-tab-pane>
          </el-tabs>
          <div class="totUp-wrap">
            数量：<b>{{totalCount}}</b>  金额：<b>{{totalMoney}}</b>
          </div>
          <div class="btn-wrap">
            <el-button type="warning" >挂单</el-button>
            <el-button type="danger" @click="delAllGoods" >删除</el-button>
            <el-button type="success" @click="checkout">结账</el-button>
          </div>

        </el-col>
        <el-col :span='17'>
          <div class="often-goods">
            <div class="title">常用商品</div>
            <div class="often-goods-list">
              <ul>
                <li v-for="oGoods in oftenGoods" @click="addOrderList(oGoods)">
                  <span>{{oGoods.goodsName}}</span>
                  <span class="o-price">￥{{oGoods.price}}</span>
                </li>
              </ul>
            </div>
          </div>
          <div class="goods-type">
            <el-tabs>
              <el-tab-pane label="汉堡">
                <ul class='cookList'>
                  <li v-for="goods in type0Goods" @click="addOrderList(goods)">
                    <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="foodPrice">￥{{goods.price}}</span>
                  </li>
                </ul>
              </el-tab-pane>
              <el-tab-pane label="小食">
                <ul class='cookList'>
                  <li v-for="goods in type1Goods" @click="addOrderList(goods)">
                    <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="foodPrice">￥{{goods.price}}</span>
                  </li>
                </ul>
              </el-tab-pane>
              <el-tab-pane label="饮料">
                <ul class='cookList'>
                  <li v-for="goods in type2Goods" @click="addOrderList(goods)">
                    <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="foodPrice">￥{{goods.price}}</span>
                  </li>
                </ul>
              </el-tab-pane>
              <el-tab-pane label="套餐">
                <ul class='cookList'>
                  <li v-for="goods in type3Goods" @click="addOrderList(goods)">
                    <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="foodPrice">￥{{goods.price}}</span>
                  </li>
                </ul>
              </el-tab-pane>              
            </el-tabs>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<style scoped>
  .btn-wrap{margin-top: 10px;}
  .title{
    height: 20px;
    border-bottom:1px solid #D3DCE6;
    background-color: #F9FAFC;
    padding:10px;
  }
  .often-goods-list{overflow: hidden}
  .often-goods-list ul li{
    list-style: none;
    float:left;
    border:1px solid #E5E9F2;
    padding:10px;
    margin:5px;
    background-color:#fff;
  }
  .pos-order{
    border-right:1px solid #ddd;
  }
  .o-price{
    color:#58B7FF;
  }
  .cookList li{
    list-style: none;
    width:23%;
    border:1px solid #E5E9F2;
    height: auto;
    overflow: hidden;
    background-color:#fff;
    padding: 2px;
    float:left;
    margin: 2px;
    cursor:pointer;
  }
  .cookList li span{
    display: block;
    float:left;
  }
  .foodImg{
    width: 40%;
  }
  .foodName{
    font-size: 18px;
    padding-left: 10px;
    color:brown;
  }
  .foodPrice{
    font-size: 16px;
    padding-left: 10px;
    padding-top:10px;
  }
  .totUp-wrap{
   padding:10px;
  }
</style>
<script>
import axios from "axios"
  export default{
    name:'Pos',
    data(){
      return{
        tableData: [],
        oftenGoods:[],
        type0Goods:[],
        type1Goods:[],
        type2Goods:[],
        type3Goods:[],
        totalMoney:0,
        totalCount:0
      }
    },    
    created(){
      axios.get('http://jspang.com/DemoApi/oftenGoods.php').then(response=>{
        this.oftenGoods=response.data;
      }).catch(error=>{
        console.log(error)
        alert('网络错误')
      });
      axios.get('http://jspang.com/DemoApi/typeGoods.php').then(response=>{
        this.type0Goods=response.data[0];
        this.type1Goods=response.data[1];
        this.type2Goods=response.data[2];
        this.type3Goods=response.data[3];
      }).catch(error=>{
        console.log(error)
      })
    },
    mounted:function(){
        var orderHeight=document.body.clientHeight;
        document.getElementById('order-list').style.height=orderHeight+'px';
    },
    methods:{
      addOrderList(goods){
        //商品是否存在ui订单列表中
        let isHave = false;
        for(let i = 0;i<this.tableData.length;i++){
          if(this.tableData[i].goodsId== goods.goodsId){
             isHave=true;
           }        
        }
        if(isHave){
          let arr = this.tableData.filter(o=>o.goodsId == goods.goodsId);
          arr[0].count++;

        }else{
          let newGoods={goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1}
          this.tableData.push(newGoods)
        }
        //总价和数量计算
        this.getAllMoney();
      },
      delSingleGoods(goods){
        this.tableData=this.tableData.filter(o=>o.goodsId != goods.goodsId);
        this.getAllMoney();
      },
      getAllMoney(){
        this.totalCount=0;
        this.totalMoney=0;
        if(this.tableData){
          this.tableData.forEach((element)=>{
            this.totalCount+=element.count;
            this.totalMoney=this.totalMoney+(element.price*element.count)
          })
        }
      },
      delAllGoods(){
        this.tableData=[];
        this.totalCount=0;
        this.totalMoney=0;
      },
      checkout(){
        if(this.totalCount!=0){
          this.tableData=[];
          this.totalCount=0;
          this.totalMoney=0;
          this.$message({
            message:'结账成功',
            type:'success'
          });
        }else{
          this.$message.error('不能空结')
        }
      }
    }
  }
</script>



